registerFileAction({
	id: 'thinkfreeEditorAction',
	displayName: () => t('thinkfree', 'Open in Thinkfree Office'),
	iconSvgInline: () => `<svg width="72" height="72" viewBox="0 0 72 72" fill="none" xmlns="http://www.w3.org/2000/svg">
		<path d="M59.0166 28.7186C55.4891 28.7186 52.6189 31.5888 52.6189 35.1163C52.6189 38.6437 55.4891 41.514 59.0166 41.514C62.544 41.514 65.4143 38.6437 65.4143 35.1163C65.4143 31.5888 62.544 28.7186 59.0166 28.7186Z" fill="currentColor" style="fill:currentColor;fill-opacity:1;"/>
		<path d="M69.4987 13H50.4644C42.9745 13 36.9038 19.0707 36.9038 26.5605V59.5364H48.5371V26.5637C48.5371 25.5001 49.4007 24.6365 50.4644 24.6365H69.5018V13H69.4987Z" fill="currentColor" style="fill:currentColor;fill-opacity:1;"/>
		<path d="M2.00317 13H21.0375C28.5274 13 34.598 19.0707 34.598 26.5605V59.5364H22.9647V26.5637C22.9647 25.5001 22.1011 24.6365 21.0375 24.6365H2V13H2.00317Z" fill="currentColor" style="fill:currentColor;fill-opacity:1;"/>
	</svg>
	`,
	enabled: (nodes) => {
		// return nodes.some(node => supportedMimeTypes.includes(node.mime))
		return nodes.some(node => supportedMimeTypeMap.has(node.mime))
	},
	// https://nextcloud-libraries.github.io/nextcloud-files/classes/index.FileAction.html#exec
	exec: (fileNode, fileList, fileDir) => {
		const dirname = fileNode.dirname
		const path = dirname === '/' ? dirname : (dirname.endsWith('/') ? dirname : dirname + '/')
		const fileName = encodeURIComponent(fileNode.basename)
		const id = fileNode.fileid
		const appMode = supportedMimeTypeMap.get(fileNode.mime)

		fetch(OC.generateUrl(`/apps/thinkfree/editor/open?path=${fileNode.dirname}&fileName=${encodeURIComponent(fileNode.basename)}&id=${fileNode.fileid}`), {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
			},
		})
			.then(response => {
				if (!response.ok) {
					throw new Error(`HTTP error! status: ${response.status}`)
				}
				return response.json()
			})
			.then(data => {
				if (data.status === 'success') {
					if (!data.appKey) {
						showError('Authentication token is invalid or missing.')
					}

					const openUrl = data.domain
						+ path
						+ fileName
						+ '/open?app=' + appMode
						+ '&docId=' + id
						+ '&appKey=' + data.appKey
						+ '&lang=' + data.lang
						+ '&usrId=' + window.OC.currentUser

					window.open(openUrl, '_blank')

					// 파일 목록 새로고침
					if (fileList && typeof fileList.reload === 'function') {
						fileList.reload()
					}
				}
			})
			.catch(() => {
				showError('Couldn’t open the document.')
			})
	},
})

OC.Plugins.register('OCA.Files.NewFileMenu', {
	attach(context) {
		const fileList = context.fileList

		context.addMenuEntry({
			id: 'new-excel',
			displayName: t('thinkfree', 'New Excel Document'),
			iconSvgInline: `
			<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
     		   <path d="M20.571 0H3.43A3.429 3.429 0 0 0 0 3.429V20.57A3.429 3.429 0 0 0 3.429 24H20.57A3.429 3.429 0 0 0 24 20.571V3.43A3.429 3.429 0 0 0 20.571 0z" fill="#1AB561"/>
   			   <path fill-rule="evenodd" clip-rule="evenodd" d="M13.299 17.22h-2.494V6.856H13.3V17.22h-.001zm-6.442 0h2.494v-6.78H6.857v6.78zm7.792 0h2.494v-4.731h-2.494v4.73z" fill="#fff"/>
			</svg>
			`,
			handler: async (context, content) => {
				try {
					const fileData = createFile('excel', '엑셀', context, content)
					if (fileData) {
						fileList.add(fileData)
					}
				} catch (e) {
					showError(e.message)
				}
			},
			order: 27,
		})
	}
})

const createFile = async (type, displayName, context, content) => {
	try {
		const response = await fetch(OC.generateUrl(`/apps/thinkfree/new_document?type=${type}&path=${encodeURIComponent(context.path)}`), {
			method: 'GET',
			headers: {
				requesttoken: window.OC.requestToken,
			},
		})

		const data = await response.json()

		if (!response.ok || data.error) {
			showError(`파일 생성 실패: ${data.error}`)
			return null
		}

		const fileData = {
			basename: data.basename,
			fileid: data.fileid,
			mimetype: data.mime,
			type: data.type, // 'file'
			path: data.path || '/',
			source: data.source,
			mtime: data.mtime,
			size: data.size
		}

		emit('files:node:created', fileData)
		setTimeout(() => {
			window.location.reload()
		}, 500)
		showSuccess(`${data.basename} 파일 생성됨`)
		return data
	} catch (error) {
		showError(`파일 생성 실패: ${error.message}`)
		return null
	}
}

